<template>
  <aside ref="fixedBlock" class="aside-container" :style="getFixedStyle">
    <div class="article-share-wrap">
      <span class="react-svg">
        <svg-icon icon-class="react" class="svg-img"/>
      </span>
      <span class="react-number">1027</span>
      <div class="share-svg-wrap">
        <svg-icon icon-class="three-dot" class="svg-img"/>
      </div>
      <div class="share-svg-wrap">
        <svg-icon icon-class="share" class="svg-img"/>
      </div>

    </div>
    <div class="share-channel-wrap">
      <svg-icon icon-class="wechat" class="svg-img"/>
      <svg-icon icon-class="sina" class="svg-img"/>
      <svg-icon icon-class="qq" class="svg-img"/>
      <svg-icon icon-class="renren" class="svg-img"/>
    </div>
  </aside>
</template>

<script>
  export default {
    mounted() {
      this.offsetTop = this.$refs.fixedBlock.getBoundingClientRect().top;
      // console.log('---->', this.$refs.fixedBlock.getBoundingClientRect());
      window.addEventListener('scroll', this.handleScroll);
      this.$once('hook:beforeDestroy', () => {
        window.removeEventListener('scroll', this.handleScroll);
      });
      window.addEventListener("beforeunload", () => {
        window.scrollTo(0, 0);
      });
    },
    methods: {
      handleScroll() {
        //console.log('handleScroll', this.offsetTop);
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop || window.pageYOffset;
        if (scrollTop > this.offsetTop) {
          this.isFixed = true;
          this.scrollTop = scrollTop;
        } else {
          this.isFixed = false
        }
      }
    },
    computed: {
      // 计算属性的 getter
      getFixedStyle() {
        if (this.isFixed) {
          let top = this.scrollTop - this.offsetTop;
          return 'top: ' + top + 'px;'
        } else {
          return {}
        }
      }
    },
    data() {
      return {
        isFixed: false,
        offsetTop: 0,
        scrollTop: 0,
      }
    },
    name: "AsideDetailLeft"
  }
</script>

<style lang="less" scoped>
  .aside-container {
    position: relative;
    width: 120px;
    height: auto;
    margin-right: 10px;

    .article-share-wrap {
      .react-number {
        margin-left: 10px;
        color: rebeccapurple;
        font-size: 24px;
      }

      .share-svg-wrap {
        font-size: 16px;
        line-height: 1.5;
      }
    }

    .share-channel-wrap {
      width: 100%;
      height: auto;
      margin-bottom: 10px;

      .svg-img {
        display: block;
        font-size: 40px;
        margin: 40px auto;
      }
    }
  }


  @media screen and (max-width: 1300px) {
    .aside-container {
      display: none;
    }
  }
</style>